body {
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f3f3f3;
  color: #444;
  font-size: 14px;
  height: 100vh;
  width: 100%;
}

.container {
  font-family: 'Rubik';
}

.header {
  text-align: center;
  padding: 0.8em 0;
  font-size: 1.7em;
  font-weight: 600;
  font-family: 'Source Sans Pro';
  color: #555;
  letter-spacing: 2px;
  text-transform: uppercase;
  border-bottom: 2px dashed #ddd;
}

.main {
  background: #fff;
  margin: 2em auto;
  min-width: 350px;
  max-width: 500px;
  box-shadow: 0 0 20px 1px #dcdcdc;
}
.itemList {
  color: #666;
  padding: 1em 0.8em;
}

.item {
  display: flex;
  background: #fff;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}
.newItem {
  width: 92%;
  display: inline-flex;
  border: 1px solid #ccc;
  padding: 0;
  margin: 10px 15px;
}
.activeItem {
  /* background: #ffcece; */
  font-size: 1.1em;
}
.activeItem label i {
  color: #ccc;
}
.activeItem label:hover span {
  text-decoration: line-through;
}
.doneItem {
  /* background: #c3ffc3; */
  font-size: 0.9em;
  color: #aaa;
}
.doneItem label i {
  color: #0eb70e;
  border-radius: 50%;
  /* box-shadow: 0 0 7px 0px green; */
}
.doneItem span {
  text-decoration: line-through;
}

.item span button {
  border-radius: 50%;
  background-color: transparent;
}
.item span button:hover {
  background-color: #eee;
}
.item label {
  width: 78%;
  display: flex;
  align-items: center;
}

.item label i {
  width: 2em;
}

.doneItem label i {
  width: 2.4em;
}

.fa-trash-alt {
  color: red;
}
.item span button {
  background-color: transparent;
  text-decoration: none !important;
  border: none;
}

input[type='text'] {
  width: 100%;
  padding: 7px 15px;
  color: #555;
}
.newItem input[type='text'] {
  width: 100%;
  padding: 10px 15px;
  color: #444;
  border: none;
}
button {
  padding: 10px;
  border: 0;
  cursor: pointer;
  color: #666;
}
.newItem button {
  background-color: #eee;
  transition: all 0.4s;
}
.newItem button:hover {
  background-color: #ccc;
}

/* Media query for mobile version */
@media only screen and (max-width: 360px) {
  .main {
    margin: 0 auto;
    height: 100vh;
  }
}
